<template>
  <div class="app-container">
    <div class="filter-container">
      <el-input
        v-model="listQuery.MenuName"
        placeholder="关键词"
        style="width: 200px"
        clearable=""
        class="filter-item"
        @keyup.enter.native="handleFilter"
      />
      <el-select
        v-model="listQuery.MenuStatus"
        placeholder="状态"
        clearable
        style="width: 90px"
        class="filter-item"
      >
        <el-option
          v-for="(item, index) in statusOptions"
          :key="index"
          :label="item"
          :value="index"
        />
      </el-select>
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        查询
      </el-button>
      <el-button
        v-waves
        class="filter-item"
        style="margin-left: 10px"
        type="primary"
        icon="el-icon-edit"
        @click="handleCreate"
      >
        新增
      </el-button>
      <el-button
        v-if="false"
        v-waves
        :loading="downloadLoading"
        class="filter-item"
        type="primary"
        icon="el-icon-download"
        @click="handleDownload"
      >
        导出
      </el-button>
    </div>

    <el-table
      :key="tableKey"
      v-loading="listLoading"
      :data="list"
      border
      row-key="Id"
      :default-expand-all="false"
      fit
      highlight-current-row
      style="width: 100%"
      height="72vh"
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
      :header-cell-style="{ 'background-color': 'rgba(245,245,245)' }"
      @sort-change="sortChange"
    >
      <el-table-column label="序号" type="index" align="center" width="80">
      </el-table-column>
      <el-table-column label="菜单名称" min-width="100px">
        <template slot-scope="{ row }">
          <span class="link-type" @click="handleDetail(row)">{{
            row.MenuName
          }}</span>
        </template>
      </el-table-column>
      <el-table-column label="菜单类型" align="center">
        <template slot-scope="{ row }">
          <div>{{ getTypeName(row.MenuType) }}</div>
        </template>
      </el-table-column>
      <el-table-column label="菜单状态" class-name="status-col">
        <template slot-scope="{ row }">
          <el-tag :type="row.MenuStatus | statusFilter">
            {{ statusOptions[row.MenuStatus] }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column label="菜单地址" align="center">
        <template slot-scope="{ row }">
          <div>{{ row.MenuUrl }}</div>
        </template>
      </el-table-column>
      <el-table-column label="显示顺序">
        <template slot-scope="{ row }">
          <span>{{ row.MenuSort }}</span>
        </template>
      </el-table-column>
      <el-table-column label="权限标识" align="center">
        <template slot-scope="{ row }">
          <div>{{ row.Authorize }}</div>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" align="center">
        <template slot-scope="{ row }">
          <span>{{ row.BaseCreateTime }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="230"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{ row, $index }">
          <el-button type="primary" size="mini" @click="handleUpdate(row)">
            编辑
          </el-button>
          <el-button
            v-if="row.MenuStatus == '1'"
            size="mini"
            type="warning"
            @click="handleModifyStatus(row, '0')"
          >
            禁用
          </el-button>
          <el-button
            v-if="row.MenuStatus == '0'"
            size="mini"
            type="success"
            @click="handleModifyStatus(row, '1')"
          >
            启用
          </el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(row, $index)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form
        ref="dataForm"
        :rules="rules"
        :model="temp"
        label-position="left"
        label-width="110px"
      >
        <el-row :gutter="0" style="width: 100%">
          <el-col :span="20" :offset="2">
            <el-form-item label="上级菜单" prop="ParentId">
              <el-cascader
                v-model="temp.ParentId"
                :show-all-levels="false"
                :options="list"
                ref="cascader"
                clearable
                :props="{
                  checkStrictly: true,
                  emitPath: false,
                  expandTrigger: 'hover',
                  value: 'Id',
                  label: 'MenuName',
                }"
                style="width: 100%"
                @change="parentMenuChange"
              ></el-cascader>
            </el-form-item>
            <el-form-item label="菜单类型" prop="MenuType">
              <el-radio-group v-model="temp.MenuType" size="normal">
                <el-radio
                  v-for="item in typeOptions"
                  :key="item.id"
                  :label="item.id"
                >
                  {{ item.label }}
                </el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="菜单名称" prop="MenuName">
              <el-input
                v-model="temp.MenuName"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item
              v-if="temp.MenuType != 1"
              label="权限标识"
              prop="Authorize"
            >
              <el-input
                v-model="temp.Authorize"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item
              v-if="temp.MenuType != 3"
              label="请求地址"
              prop="MenuUrl"
            >
              <el-input
                v-model="temp.MenuUrl"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item label="显示顺序" prop="MenuSort">
              <el-input
                v-model="temp.MenuSort"
                :readonly="dialogStatus === 'detail'"
              />
            </el-form-item>
            <el-form-item
              v-if="temp.MenuType != 2"
              label="菜单图标"
              prop="MenuIcon"
            >
              <el-input
                v-model="temp.MenuIcon"
                :readonly="dialogStatus === 'detail'"
                @focus="handleIcon"
              />
            </el-form-item>
            <el-form-item label="菜单状态" prop="MenuStatus">
              <el-radio-group v-model="temp.MenuStatus" size="normal">
                <el-radio
                  v-for="(item, index) in statusOptions"
                  :key="index"
                  :label="index"
                >
                  {{ item }}
                </el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div v-if="dialogStatus !== 'detail'" slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> 取消 </el-button>
        <el-button
          type="primary"
          @click="dialogStatus === 'create' ? createData() : updateData()"
        >
          确认
        </el-button>
      </div>
    </el-dialog>
    <el-dialog title="菜单图标" :visible.sync="dialogIconVisible">
      <div class="icons-container">
        <el-tabs type="border-card">
          <el-tab-pane label="Icons">
            <div class="grid">
              <div
                v-for="item of svgIcons"
                :key="item"
                @click="handleSelectIcon(generateIconCode(item), $event)"
              >
                <el-tooltip placement="top">
                  <div slot="content">
                    {{ generateIconCode(item) }}
                  </div>
                  <div class="icon-item">
                    <svg-icon :icon-class="item" class-name="disabled" />
                    <span>{{ item }}</span>
                  </div>
                </el-tooltip>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="Element-UI Icons">
            <div class="grid">
              <div
                v-for="item of elementIcons"
                :key="item"
                @click="handleSelectIcon(generateElementIconCode(item), $event)"
              >
                <el-tooltip placement="top">
                  <div slot="content">
                    {{ generateElementIconCode(item) }}
                  </div>
                  <div class="icon-item">
                    <i :class="'el-icon-' + item" />
                    <span>{{ item }}</span>
                  </div>
                </el-tooltip>
              </div>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogIconVisible = false"> 取消 </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import svgIcons from "./svg-icons";
import elementIcons from "./element-icons";
import { fetchList, saveMenu, deleteMenu } from "@/api/menu";
import waves from "@/directive/waves"; // waves directive
import { parseTime } from "@/utils";

export default {
  name: "RoleIndex",
  components: {},
  directives: { waves },
  filters: {
    statusFilter(Status) {
      const statusTypes = ["info", "success"];
      return statusTypes[Status];
    },
  },
  data() {
    return {
      svgIcons,
      elementIcons,
      menus: [],
      defaultProps: {
        children: "children",
        label: "MenuName",
      },
      tableKey: 0,
      list: null,
      listLoading: true,
      listQuery: {
        MenuName: "",
        MenuStatus: undefined,
        PageIndex: 1,
        PageSize: 12,
      },
      typeOptions: [
        { label: "目录", id: 1 },
        { label: "页面", id: 2 },
        { label: "按钮", id: 3 },
      ],
      statusOptions: ["禁用", "启用"],
      temp: {
        Id: 0,
        ParentId: 0,
        MenuName: "",
        MenuIcon: "",
        MenuUrl: "",
        MenuTarget: "",
        MenuSort: 0,
        MenuType: 1,
        MenuStatus: 1,
        Authorize: "",
        Remark: "",
        ParentName: "",
      },
      dialogFormVisible: false,
      dialogIconVisible: false,
      dialogStatus: "",
      textMap: {
        update: "菜单编辑",
        create: "菜单新增",
        detail: "菜单详情",
      },
      rules: {
        MenuName: [
          { required: true, message: "菜单名称必填", trigger: "blur" },
        ],
      },
      downloadLoading: false,
    };
  },
  created() {
    this.getList();
  },
  methods: {
    getTypeName(typeId) {
      let name = "";
      let type = this.typeOptions.find((_) => _.id == typeId);
      if (type) {
        name = type.label;
      }
      return name;
    },
    getList() {
      this.listLoading = true;
      fetchList(this.listQuery)
        .then((response) => {
          let data = response.Data;
          let firstMenus = [];
          let secondMenus = [];
          let thirdMenus = [];
          data.forEach((element) => {
            if (element.MenuType == 1) {
              firstMenus.push(element);
            }
            if (element.MenuType == 2) {
              secondMenus.push(element);
            }
            if (element.MenuType == 3) {
              thirdMenus.push(element);
            }
          });
          this.addMenuChildren(secondMenus, thirdMenus);
          this.addMenuChildren(firstMenus, secondMenus);

          this.list = firstMenus;
          this.listLoading = false;
        })
        .catch((error) => {
          this.listLoading = false;
        });
    },
    handleFilter() {
      this.listQuery.PageIndex = 1;
      this.getList();
    },
    handleModifyStatus(row, Status) {
      const tempData = Object.assign({}, row);
      tempData.MenuStatus = Status;
      saveMenu(tempData)
        .then(() => {
          this.getList();
          this.$notify({
            title: "成功",
            message: "操作成功",
            type: "success",
            duration: 2000,
          });
        })
        .catch((error) => {
          this.$message({
            title: "失败",
            message: "操作失败",
            type: "error",
          });
        });
    },
    sortChange(data) {
      const { prop, order } = data;
      if (prop === "id") {
        this.sortByID(order);
      }
    },
    sortByID(order) {
      if (order === "ascending") {
        this.listQuery.sort = "+id";
      } else {
        this.listQuery.sort = "-id";
      }
      this.handleFilter();
    },
    resetTemp() {
      this.temp = {
        Id: 0,
        ParentId: 0,
        MenuName: "",
        MenuIcon: "",
        MenuUrl: "",
        MenuTarget: "",
        MenuSort: 0,
        MenuType: 1,
        MenuStatus: 1,
        Authorize: "",
        Remark: "",
        ParentName: "",
      };
    },
    addMenuChildren(parentMenus, menus) {
      if (parentMenus && parentMenus.length > 0) {
        parentMenus = parentMenus.map((item) => {
          let children = menus.filter((_) => _.ParentId == item.Id);
          if (children && children.length > 0) {
            item.children = children;
          }
          return item;
        });
      }
    },
    getMenusTree() {
      fetchMenuList()
        .then((response) => {
          let data = response.Data;
          let firstMenus = [];
          let secondMenus = [];
          let thirdMenus = [];
          data.forEach((element) => {
            if (element.MenuType == 1) {
              firstMenus.push(element);
            }
            if (element.MenuType == 2) {
              secondMenus.push(element);
            }
            if (element.MenuType == 3) {
              thirdMenus.push(element);
            }
          });
          this.addMenuChildren(secondMenus, thirdMenus);
          this.addMenuChildren(firstMenus, secondMenus);
          this.menus = firstMenus;
        })
        .catch();
    },
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    createData() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          saveMenu(this.temp)
            .then(() => {
              this.getList();
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "新增成功！",
                type: "success",
                duration: 2000,
              });
            })
            .catch((error) => {
              this.$notify({
                title: "失败",
                message: "新增失败！",
                type: "error",
                duration: 2000,
              });
            });
        }
      });
    },
    handleDetail(row) {
      this.temp = Object.assign({}, row); // copy obj
      this.dialogStatus = "detail";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    handleUpdate(row) {
      this.temp = Object.assign({}, row); // copy obj
      this.temp.timestamp = new Date(this.temp.timestamp);
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    updateData() {
      this.$refs["dataForm"].validate((valid) => {
        if (valid) {
          const tempData = Object.assign({}, this.temp);
          saveMenu(tempData)
            .then(() => {
              this.getList();
              this.dialogFormVisible = false;
              this.$notify({
                title: "成功",
                message: "操作成功！",
                type: "success",
                duration: 2000,
              });
            })
            .catch((error) => {
              this.$notify({
                title: "失败",
                message: "操作失败！",
                type: "error",
                duration: 2000,
              });
            });
        }
      });
    },
    handleDelete(row, index) {
      this.$confirm("此操作将删除该数据, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then((_) => {
          deleteMenu({ Ids: row.Id })
            .then((response) => {
              this.$notify({
                title: "成功",
                message: "删除成功",
                type: "success",
                duration: 2000,
              });
              this.getList();
            })
            .catch((error) => {
              this.$notify({
                title: "失败",
                message: "删除失败！",
                type: "error",
                duration: 2000,
              });
            });
        })
        .catch((_) => {});
    },
    handleIcon() {
      if (this.dialogStatus === "detail") {
        return;
      }
      this.dialogIconVisible = true;
    },
    generateIconCode(symbol) {
      // return `<svg-icon icon-class="${symbol}" />`;
      return symbol;
    },
    generateElementIconCode(symbol) {
      // return `<i class="el-icon-${symbol}" />`;
      return `el-icon-${symbol}`;
    },
    handleSelectIcon(text, event) {
      this.temp.MenuIcon = text;
      this.dialogIconVisible = false;
    },
    handleDownload() {
      this.downloadLoading = true;
      import("@/vendor/Export2Excel").then((excel) => {
        const tHeader = [
          "菜单名称",
          "菜单类型",
          "菜单状态",
          "菜单地址",
          "显示顺序",
          "权限标识",
          "创建时间",
        ];
        const filterVal = [
          "MenuName",
          "MenuType",
          "MenuStatus",
          "MenuUrl",
          "MenuSort",
          "Authorize",
          "BaseCreateTime",
        ];
        const data = this.formatJson(filterVal);
        excel.export_json_to_excel({
          header: tHeader,
          data,
          filename: "菜单列表",
        });
        this.downloadLoading = false;
      });
    },
    formatJson(filterVal) {
      return this.list.map((v) =>
        filterVal.map((j) => {
          if (j === "timestamp") {
            return parseTime(v[j]);
          } else {
            return v[j];
          }
        })
      );
    },
    parentMenuChange() {
      // this.$refs.cascader;
    },
  },
};
</script>
<style lang="scss" scoped>
.tree-container {
  overflow-y: auto;
  max-height: 300px;
}
.icons-container {
  margin: 10px 20px 0;

  .grid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    height: 44vh;
    margin: 10px 20px 0;
    overflow: auto;
  }

  .icon-item {
    margin: 20px;
    height: 85px;
    text-align: center;
    width: 100px;
    float: left;
    font-size: 30px;
    color: #24292e;
    cursor: pointer;
  }

  span {
    display: block;
    font-size: 16px;
    margin-top: 10px;
  }

  .disabled {
    pointer-events: none;
  }
}
</style>
